.Building {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.Building .image-container {
  display: block;
  margin-left: 1rem;
  margin-bottom: 1rem;
  margin-top: 1rem;
  width: 550px;
  height: 550px;
  border-style: ridge;
  border-width: 5px;
  border-color: white;
}

.address-title {
  position: absolute;
  left: 32rem;
  margin-top: 0.8rem;
  margin-left: 1rem;
  font-size: 50px;
  font-weight: 500;
}

.Building .information-container {
  position: absolute;
  display: flex;
  flex-direction: column;
  left: 32rem;
  margin-left: 1rem;
  margin-top: 5rem;
  max-height: 550px;
}

.paravar {
  margin-bottom: 1.5rem;
  display: flex;
  flex-direction: row;
}

.parameter {
  width: auto;
  font-size: 35px;
  margin-bottom: 0.85rem;
  display: flex;
  justify-content: left;
}

.parameter span {
  background-color: rgb(33, 119, 248);
  border-radius: 1.5rem;
  padding: 1.5rem 1.5rem 1.5rem 1.5rem;
}

.variable {
  position: relative;
  font-size: 40px;
  font-weight: 550;
  text-align: right;
  left: -1rem;
  top: 4rem;
}

.variable span {
  background-color: rgb(58, 87, 252);
  padding: 0.5rem 0.5rem 0.5rem 0.5rem;
  border-radius: 1rem;
  min-width: 10rem;
}

.cVariable {
  position: relative;
  font-size: 40px;
  font-weight: 550;
  text-align: right;
  left: -1rem;
  top: 4rem;
  max-width: 4rem;
  max-height: 2rem;
  background-color: rgb(53, 39, 252);
  padding: 0.5rem 0.5rem 0.5rem 0.5rem;
  border-radius: 1rem;
  border-style: none;
  color: white;
}

#i2 {
  left: 55rem;
}

#i3 {
  left: 77rem;
}


.typeContainer {
  display: flex;
  flex-direction: row;
  width: 500px;
  height: 10rem;
  margin-bottom: 2rem;
}

.typeContainer h2 {
  font-weight: 550;
  font-size: 25px;
  margin-bottom: 0.5rem;
}

.compTypeContainer {
  margin-left: 1rem;
  width: 9.85rem;
  height: 12rem;
}

.energyContainer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  height: 120px;
  background-color: rgb(5, 46, 107);
  border-top: 3px solid white;
  border-bottom: 3px solid white;
}

.energyData {
  font-size: 23px;
  margin: auto 0rem auto 1rem;
  font-weight: 550;
}

.energyData2 {
  font-size: 25px;
  margin: auto 1rem auto 1rem;
}

