
.materialLists {
    display: flex;
    flex-direction: row;
    width: 100%;
    margin-top: 2rem;
    justify-content: center;
}

.materialSection {
    width: 20%;
    margin: 0 1rem 0 1rem;
}

.dropCreate {
    display: inline-flex;
    width: 100%;
}

.drop1 {
    width: 50%;
    font-weight: 550;
}

.materialSection li{
    margin-top: 0.3rem;
    list-style-type: disc;
}

.materialSection h2{
    margin-bottom: 0.5rem;
}

.materialSection button{
    font-size: 16px;
    color: white;
    background-color: rgb(72, 109, 209);
    border-color:  rgb(72, 109, 209);
    border-style: solid;
    border-radius: 5px;
    font-family: 'Rubik', sans-serif;
    margin-top: 0.5rem;
}
